.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.grid-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.grid-cell {
  width: 15px;
  height: 15px;

  border-top: 1px solid #000;
  border-left: 1px solid #000;
}

.grid-cell-border {
  background-color: #000;
}

.grid-cell-snake {
  background-color: #000;
}

.grid-cell-snack {
  background-color: #000;
}

.grid-cell-hit {
  background-color: #f00;
}



